'use strict';

import React from 'react';
import {BrowserRouter as Router, Link, Route} from 'react-router-dom';

// 
import HomeNavComponent from '../components/HomeNavComponent'

// 
import BottomComponent from '../components/BootomComponent'

import ToTopComponent from  '../components/ToTopComponent'

require('styles//NavRouter.css');


class NavRouterComponent extends React.Component {
  render() {
    return (
      <Router>
        <div>
          <div>
            {/*nav*/}
            <div  className={'first-nav flex'}>
              <div className={'logo'}>
                <div></div>
              </div>
              <NavBtn activeOnlyWhenExact={true} to="/discover" label="" />
              <NavBtn to="/about" label="" />
              <NavBtn to="/topics" label="" />
              <div className={'fnavbtn'}>
                <a href="https://music.163.com/store/product" target="_blank"></a>
              </div>
              <div className={'fnavbtn'}>
                <a href="https://music.163.com/nmusician/web/index" target="_blank"></a>
              </div>
              <div className={'fnavbtn'}>
                <a href="https://music.163.com/#/download" target="_blank"></a>
                <span className={'fnavbtn-hot'}></span>
              </div>
              <div className={'fnavbtn-search absolute-center'}>
                <input type="text" placeholder={'///'} />
                <span className={'search-logo'}></span>
              </div>
              <div className={'fnavbtn-submit absolute-center'}>
                <a href="https://music.163.com/#/login?targetUrl=%2Fuservideo" target="_blank"></a>
              </div>
              <div className={'fnavbtn-login absolute-center'}>
                <a href="#">
                  <span></span>
                </a>
                <div className={'login-list'}>
                  <div className={'login-phone'}><span></span></div>
                  <div className={'login-wechat'}><span></span></div>
                  <div className={'login-qq'}><span></span>QQ</div>
                  <div className={'login-sina'}><span></span></div>
                  <div className={'login-wangyi'}><span></span></div>
                </div>
              </div>
            </div>
          </div>

          {/*nav*/}
          <div>
            <Route exact path="/discover" component={HomeNavComponent} />
            <Route path="/about" component={Mine} />
            <Route path="/topics" component={Friend} />
          </div>

          {/**/}
          <BottomComponent />

          {/**/}
          <ToTopComponent />
        </div>


      </Router>
    );
  }
}


// navbtn
const NavBtn = ({ label, to, activeOnlyWhenExact }) => (
  <Route
    path={to}
    exact={activeOnlyWhenExact}
    children={({ match }) => (
      <div className={match ? 'active fnavbtn' : 'fnavbtn'}>
        {match ? (<span className="nav-triangle"></span>) : ''}
        <Link to={to}>{label}</Link>
      </div>
    )}
  />
);

const Home = ({ match }) => (
  <div>
    <h2>Topics</h2>
    <ul>
      <li>
        <Link to={`${match.url}/rendering`}>Rendering with React</Link>
      </li>
      <li>
        <Link to={`${match.url}/components`}>Components</Link>
      </li>
      <li>
        <Link to={`${match.url}/props-v-state`}>Props v. State</Link>
      </li>
    </ul>

    <Route path={`${match.url}/:topicId`} component={Topic} />
    <Route
      exact
      path={match.url}
      render={() => <h3>Please select a topic.</h3>}
    />
  </div>
);

const Mine = () => (
  <div>
    <h2>About</h2>
  </div>
);

const Friend = () => (
  <div>
    <h2>About</h2>
  </div>
);

const Topic = ({ match }) => (
  <div>
    <h3>{match.params.topicId}</h3>
  </div>
);

// NavRouterComponent.displayName = 'NavRouterComponent';

// Uncomment properties you need
NavRouterComponent.propTypes = {};
NavRouterComponent.defaultProps = {};

export default NavRouterComponent;



