'use strict';

import React from 'react';
import { BrowserRouter as Router, Route, Link } from "react-router-dom";

// 
import CarouselComponent from 'components/CarouselComponent';

// 
import ContentComponent from 'components/ContentComponent';
require('styles//HomeNav.css');

const Img=[
  require('../images/1.jpg'),
  require('../images/2.jpg'),
  require('../images/3.jpg'),
  require('../images/4.jpg'),
  require('../images/5.jpg'),
  require('../images/6.jpg')
];

const HomeNavComponent = ({ match }) => (
  <Router>
    <div>
      <div className={'second-nav flex'}>
        <NavBtn activeOnlyWhenExact={true} to={`${match.url}`} label="" />
        <NavBtn to={`${match.url}/toplist`} label="" />
        <NavBtn to={`${match.url}/playlist`} label="" />
        <NavBtn to={`${match.url}/djradio`} label="" />
        <NavBtn to={`${match.url}/artist`} label="" />
        <NavBtn to={`${match.url}/album`} label="" />
        <span className={'blank'}>2333333333</span>
      </div>
      <div className={'content'}>
        <Route path={`${match.url}/:Id`} component={Topic} />
        <Route
          exact
          path={match.url}
          render={() => <div>
              <CarouselComponent
                images={Img}
                speed={1}
                delay={4}
                autoPlay={true}
                autoParse={true}
              /><ContentComponent />
            </div>
            }
        />
      </div>

    </div>
  </Router>
);

// navbtn
const NavBtn = ({ label, to, activeOnlyWhenExact }) => (
  <Route
    path={to}
    exact={activeOnlyWhenExact}
    children={({ match }) => (
      <div className={match ? 'active s-navbtn' : 's-navbtn'}>
        <Link to={to}>{label}</Link>
      </div>
    )}
  />
);

const Topic = ({ match }) => (
  <div>
    <h3>{match.params.Id}</h3>
  </div>
);

// HomeNavComponent.displayName = 'HomeNav';

// Uncomment properties you need
HomeNavComponent.propTypes = {

};
HomeNavComponent.defaultProps = {

};

export default HomeNavComponent;



